import React, {useState, useEffect} from "react"
import { Link, useStaticQuery, graphql } from "gatsby"
import Navbar from "react-bootstrap/Navbar"
import Nav from "react-bootstrap/Nav"
import Rainbow from "./rainbow"
import layoutStyles from "./layout.module.css"
import WindowSize from "../../utilities/WindowSize"

const Header = () => {
  const data = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            githubUrl
            instagramUrl
          }
        }
      }
    `
  )

  const slogans = data.site.siteMetadata.description.split(".").filter(Boolean);
  
  let [index, setIndex] = useState(0);

  const incrementIndex = () => {setIndex((index + 1) % slogans.length)}

  let window = WindowSize();

  return (
    <header>
      <Navbar className={layoutStyles.navbar} expand="lg">
        <Navbar.Brand className={layoutStyles.brand}>
          <Link to="/">
            <div className={`${window.width >= 992 ? layoutStyles.navbarLogoBig : layoutStyles.navbarLogoSmall}`} ></div>
          </Link>
        <Navbar.Text className={`${layoutStyles.description} d-none d-lg-block`} onAnimationIteration={incrementIndex}>{slogans[index]}</Navbar.Text>
        </Navbar.Brand>
        
        <Navbar.Toggle/>
        <Navbar.Collapse className={layoutStyles.navigation}>
            <Nav className={layoutStyles.primary}>
              <Nav.Item>
                <Link to="/stories/" className={layoutStyles.button}>
                  Stories
                </Link>
              </Nav.Item>
              <Nav.Item>
                <Link to="/team/" className={layoutStyles.button}>
                  Team
                </Link>
              </Nav.Item>
              <Nav.Item>
                <Link to="/award/" className={layoutStyles.button}>
                  Awards
                </Link>
              </Nav.Item>
              <Nav.Item>
                <Link to="/contact/" className={layoutStyles.button}>
                  Contact Us
                </Link>
              </Nav.Item>
            </Nav>
            <Nav className={layoutStyles.primary}>
              <Nav.Item> 
                <a href={data.site.siteMetadata.instagramUrl}>
                  <div role="button" aria-label="Instagram" className={`${layoutStyles.button} ${layoutStyles.icon} ${layoutStyles.instagram}`}></div>
                </a>
              </Nav.Item>
              <Nav.Item>
                <a href={data.site.siteMetadata.githubUrl}> 
                  <div role="button" aria-label="GitHub" className={`${layoutStyles.button} ${layoutStyles.icon} ${layoutStyles.github}`}></div>
                </a>
              </Nav.Item>
            </Nav>
        </Navbar.Collapse>
      </Navbar>
      <Rainbow/>
    </header>
  )
}

export default Header
